header.container {
  width: 100%;
  padding-bottom: 60px;
  display: flex;
  flex-direction: column;
  position: relative;
}

header.container div.elipse-container {
  width: 90%;
  height: 250px;
  margin: 0 auto;

  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
}

/*_________________________________________*/
/*              ELIPSE LEFT                */
/*_________________________________________*/

header.container div.elipse-container div.elipse-left {
  max-width: 600px;
  height: 250px;
  display: flex;
  flex-flow: row;
  position: relative;
}

header.container div.elipse-container div.elipse-left div.circle-left-one {
  position: absolute;
  left: 0;
  bottom: 35px;
}

header.container div.elipse-container div.elipse-left div.circle-left-two {
  position: absolute;
  left: 50;
  top: 25px;
}

header.container div.elipse-container div.elipse-left div.circle-left-three {
  position: absolute;
  left: 140px;
  top: 35px;
}

/*_________________________________________*/
/*              ELIPSE RIGHT                */
/*_________________________________________*/

header.container div.elipse-container div.elipse-right {
  max-width: 600px;
  height: 250px;
  display: flex;
  flex-flow: row;
  position: absolute;
  right: 0;
}

header.container div.elipse-container div.elipse-right div.circle-right-one {
  position: absolute;
  right: 350px;
  bottom: 20px;
}

header.container div.elipse-container div.elipse-right div.circle-right-two {
  position: absolute;
  right: 310px;
  top: 22px;
}

header.container div.elipse-container div.elipse-right div.circle-right-three {
  position: absolute;
  right: 190px;
  bottom: 0px;
}

header.container div.elipse-container div.elipse-right div.circle-right-four {
  position: absolute;
  right: 130px;
  bottom: 15px;
}

/*_________________________________________*/
/*              Informações e Form         */
/*_________________________________________*/

header.container div.information-container {
  width: 100%;
  padding-bottom: 5rem;
  margin-top: -2.5rem;

  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
}

header.container div.information-container div.information-left {
  display: flex;
  flex-direction: column;
  justify-content: left;

  max-width: 700px;
}

header.container
  div.information-container
  div.information-left
  div.information-text {
  max-width: 90%;
}

header.container
  div.information-container
  div.information-left
  div.information-text
  h3 {
  color: #6f6f6f;
  font-size: max(17px, 1.1vw);
}

header.container
  div.information-container
  div.information-left
  div.information-text
  h1 {
  font-size: max(22px, 2.5vw);
  margin-top: 2rem;
  margin-bottom: 1rem;
}

header.container
  div.information-container
  div.information-left
  div.information-text
  h4 {
  max-width: 95%;
  margin-bottom: 1.5rem;
  font-size: (16px, 1.2vw);
}

header.container
  div.information-container
  div.information-left
  div.information-text
  p {
  max-width: 95%;
  margin-bottom: 1.5rem;
  font-weight: 300;
}

header.container
  div.information-container
  div.information-left
  div.information-img {
  width: 200px;
  height: 300px;
  
}

header.container
  div.information-container
  div.information-left
  div.information-img
  img {
  max-width: 100%;
  
}

div.information-img{
  border: solid;
    

}

/*   right          */

header.container div.information-container div.information-right {
  display: flex;
  flex-direction: column;
  max-width: 600px;
  margin-top: 5.2rem;
}

header.container div.information-container div.information-right form {
  max-width: 100%;
  background-color: #fff;
  border-radius: 10px;
  padding: 40px 15px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

header.container
  div.information-container
  div.information-right
  form
  div.form-input {
  display: flex;
  flex-direction: column;
  width: 90%;
  margin-bottom: 1.5rem;
}

header.container
  div.information-container
  div.information-right
  form
  div.form-input
  input {
  height: 40px;
  margin-bottom: 5px;
}

header.container
  div.information-container
  div.information-right
  form
  div.form-input
  label.label-input {
  font-size: max(16px, 0.9vw);
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
}

header.container
  div.information-container
  div.information-right
  form
  div.form-text {
  max-width: 90%;
  text-align: left;
  margin-top: 2rem;
  margin-bottom: 1.2rem;
}

header.container
  div.information-container
  div.information-right
  form
  div.form-text
  span {
  font-size: max(15px, 0.9vw);
  font-family: "Open Sans", sans-serif;
  font-weight: 300;
}

header.container
  div.information-container
  div.information-right
  form
  div.form-enquete {
  width: 90%;
  margin-bottom: 1.2rem;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
}

header.container
  div.information-container
  div.information-right
  form
  div.form-enquete
  input {
  margin-right: 5px;
}

header.container
  div.information-container
  div.information-right
  form
  button.form-button {
  width: 60%;
  height: 60px;
  background-color: #3c6401;
  border: none;
  border-radius: 50px;
  margin-top: 3rem;
  margin-bottom: 3rem;

  color: #fff;
  font-size: max(16px, 1.1vw);
  font-weight: bold;
  transition: 0, 8s ease-out;
}

header.container
  div.information-container
  div.information-right
  form
  button.form-button:hover {
  opacity: 0.8;
}

/*_________________________________________*/
/*              Last section                */
/*              White part                  */
/*_________________________________________*/

main {
  width: 100%;
  padding: 20px 10px;
  background-color: #fff;
}

main section.services {
  max-width: 800px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin: 4rem auto;
}

main section.services span {
  font-size: max(18px, 0.9vw);
  font-family: "Open Sans", sans-serif;
  font-weight: bold;
}

/*_________________________________________*/
/*             SECTION SOCIAL MEDIA        */
/*_________________________________________*/

main section.social-media {
  width: 100%;
  margin-bottom: 5rem;
  margin-top: 1rem;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: center;
}

main section.social-media div.contact {
  display: flex;
  flex-flow: row wrap;
  margin: 0 1rem;
}

main section.social-media div.contact a {
  display: flex;
  flex-flow: row wrap;
  text-decoration: none; /* tira sublinhado*/
}

main section.social-media div.contact a ion-icon {
  color: blue;
  font-size: 1.8rem;
  margin-right: 5px;
}

main section.social-media div.contact a span {
  font-size: max(14px, 0.9vw);
  font-family: "Open Sans", sans-serif;
  font-weight: 300;
  color: black;
}

/*________________________________________*/
/*             FOOTER                      */
/*_________________________________________*/

footer {
  width: 100%;
  background-color: #fff;
  padding: 5rem 1rem;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

footer a {
  text-decoration: none;
  padding: 1.5rem 8rem;
  border: 1px solid #000;
  border-radius: 50px;
  color: #000;
  text-shadow: 2px 2px 4px #494747;
  font-size: max(20px, 1.8vw);
  font-family: "Open Sans", sans-serif;
  box-shadow: 2px 4px #000;
  transition: 0.8s ease-out;
}

footer a:hover {
  background-color: #3c6401;
  color: #fff;
}
